<template>
  <div>
    <h1>{{ $t('news.newsFeed') }}</h1>
    <b-input size="sm" v-model="feedUrl" @input="fetchData" /><br />
    <table>
      <template v-for="(data, idx) in items">
        <tr :key="idx">
          <td>
            {{ data.title }}
          </td>
          <td v-if="showImages">
            <span v-html="data['content:encoded']" />
          </td>
          <td v-else>
            <span v-html="data.content" />
          </td>
        </tr>
      </template>
    </table>
  </div>
</template>

<script>
import RSSParser from 'rss-parser'

export default {
  name: 'news',
  data () {
    return {
      feedUrl: 'https://www.n-tv.de/politik/rss',
      items: []
    }
  },
  methods: {
    async fetchData () {
      this.items = []
      const parser = new RSSParser()
      const CORS_PROXY = 'https://cors-anywhere.herokuapp.com/'
      const feed = await parser.parseURL(CORS_PROXY + this.feedUrl)
      feed.items.forEach(item => {
        this.items.push(item)
      })
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style scoped>
  table {
    width: 100%;
  }

  td {
    text-align: left;
    vertical-align: text-top;
  }

  tr {
    border-bottom: 1px solid black;
  }
</style>
